*,
*::before,
*::after {
  --darkBlue: #011627;
  --red: #f71735;
  --teal: #41ead4;
  --white: #fdfffc;
  --yellow: #ff9f1c;

  --background: #fafafa;
  --bg-white-custom: rgba(255, 255, 255, 1);

  --transition: all ease-in-out 0.4s;

  /* product 2 */
  --color1: #232323;
  --color2: #fff;
  --color3: #9bdc28;
  --transitionDuration: 1s;
  /* product 2 */

  /* product 4 */
  --color-blue: #4285f4;
  /* product 4 */

  /* product 7 */
  --white-100: #fff;
  --white-200: #e4e6e7;
  --white-300: #c9cccf;
  --white-400: #a1a6aa;
  --white-500: #798086;
  --black-100: #0a0d0f;
  --black-200: #080a0c;
  --black-300: #060809;
  --black-400: #040506;
  --black-500: #020303;
  --brown-100: #d0c8c9;
  --brown-200: #b8adae;
  --brown-300: #a09293;
  --brown-400: #887778;
  --brown-500: #6d5f60;
  --text-xs: clamp(0.82rem, calc(0.8rem + 0.14vw), 0.9rem);
  --text-sm: clamp(0.91rem, calc(0.88rem + 0.14vw), 1rem);
  --text-md: clamp(1rem, calc(0.97rem + 0.14vw), 1.08rem);
  --text-lg: clamp(1.09rem, calc(1.06rem + 0.14vw), 1.18rem);
  --text-xl: clamp(1.19rem, calc(1.16rem + 0.14vw), 1.27rem);
  --text-xxl: clamp(1.28rem, calc(1.25rem + 0.14vw), 1.38rem);
  --display-xs: clamp(1.56rem, calc(1.44rem + 0.55vw), 1.94rem);
  --display-sm: clamp(1.95rem, calc(1.75rem + 0.92vw), 2.57rem);
  --display-md: clamp(2.44rem, calc(2.11rem + 1.45vw), 3.42rem);
  --display-lg: clamp(3.05rem, calc(2.55rem + 2.22vw), 4.55rem);
  --display-xl: clamp(3.82rem, calc(3.07rem + 3.32vw), 6.05rem);
  --display-xxl: clamp(4.77rem, calc(3.67rem + 4.86vw), 8.05rem);
  --shadow-small: 0 1px 3px 0 rgba(0, 0, 0, 0.1),
    0 1px 2px 0 rgba(0, 0, 0, 0.06);
  --shadow-medium: 0 4px 6px -1px rgba(0, 0, 0, 0.1),
    0 2px 4px -1px rgba(0, 0, 0, 0.06);
  --shadow-large: 0 10px 15px -3px rgba(0, 0, 0, 0.1),
    0 4px 6px -2px rgba(0, 0, 0, 0.05);
  /* product 7 */

  /* notused but can be useful */
  --color-red: #db4437;
  --color-yellow: #f4b400;
  --color-green: #0f9d58;
  /* notused but can be useful */

  list-style: none;
  /* list-style-type: none; */
  text-decoration: none;
}

/*********************************************** basics COMMON *************************************************************/

::-moz-selection {
  /* Code for Firefox */
  background: #ffc107;
  color: #fff;
}
::selection {
  background: #ffc107;
  color: #fff;
}

.shadow-s {
  box-shadow: var(--shadow-small);
}
.shadow-m {
  box-shadow: var(--shadow-medium);
}
.shadow-l {
  box-shadow: var(--shadow-large);
}
.pointer {
  cursor: pointer;
}

.display-block {
  display: block !important;
}
.display-none {
  display: none !important;
}

.clr-red {
  color: var(--red) !important;
}

.font-weight-100 {
  font-weight: 100;
}

a:hover {
  color: #070c17;
  text-decoration: none;
  /* letter-spacing: 1px; */
  /* transform: scale(1.01); */
  transform: none;
}
a,
button {
  cursor: pointer;
  user-select: none;
  border: none;
  outline: none;
  background: none;
  text-decoration: none;
  color: #414141;
}
h1,
h2,
h3,
h4,
h5,
h6,
p {
  text-wrap: balance;
}
img {
  image-rendering: -webkit-optimize-contrast;
  image-rendering: -moz-crisp-edges;
  image-rendering: crisp-edges;
}
