/*--------------------------------- the scroll up n down animation --------------------------------- */
.text-stroke-container {
  position: relative;
  z-index: 99;
  background: #ffeb3b;
  padding: 30px;
  display: flex;
  justify-content: space-evenly;
  height: 270px;
  overflow: hidden;
}
.text-stroke-container:hover .slow1,
.text-stroke-container:hover .slow2,
.text-stroke-container:hover .slow3 {
  animation-play-state: paused;
}
.text-stroke-style {
  -webkit-text-stroke: 2px aquamarine;
  text-shadow: -4px 2px #fd0000;
}
.tex {
  filter: drop-shadow(4px 2px 1px black);
  color: #ffeb3b;
  font-size: 51px;
}
.te {
  -webkit-text-stroke-width: 2px;
  -webkit-text-stroke-color: red;
  font-size: 31px;
  font-weight: 900;
  color: #ffeb3b;
}

.scrolldown {
  position: absolute;
  top: -169%;
}
.slow1 {
  animation: scrolldown 3s infinite linear;
  right: 38%;
}
.slow2 {
  animation: scrolldown 2s infinite linear;
  right: 5%;
}
.slow3 {
  animation: scrolldown 2s infinite linear;
  left: 5%;
}
@keyframes scrolldown {
  0% {
    transform: translate(0, 0);
  }

  100% {
    transform: translate(0, 10%);
  }
}

/*--------------------------------- the scroll up n down animation --------------------------------- */

.textclip-container {
  height: 70vh;
  background-color: #1d2a3a;
  display: grid;
  place-items: center;
}
.textClip {
  font-size: 8px;
  line-height: 10px;
  /* background: url("./../images/newImg//products/applap_395f1832-31fa-4105-b32b-8c273816d3e76a4c.png")
    fixed center no-repeat;
  background: url("./../images/newImg//products/one1plus_6a561c17-dde7-4500-a75d-d0caf41de55800b0.png")
    center no-repeat; */
  background-size: contain;
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: rgba(255, 255, 255, 0.1);
}

/*********************************************** LOADER ************************************************* */
.loader {
  width: 100vw;
  height: 100vh;
  position: absolute;
  top: 0;
  left: 0;
  background-color: var(--darkBlue);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  z-index: 999;
}
/*********************************************** LOADER ************************************************* */


/*********************************************** custom LOADER ************************************************* */
.custom-loader {
  width:50px;
  height:24px;
  background: 
    radial-gradient(circle closest-side,#766DF4 90%,#0000) 0%   50%,
    radial-gradient(circle closest-side,#766DF4 90%,#0000) 50%  50%,
    radial-gradient(circle closest-side,#766DF4 90%,#0000) 100% 50%;
    background: radial-gradient(circle closest-side,#e7e7e7 90%,#0000) 0% 50%, radial-gradient(circle closest-side,#e7e7e7 90%,#0000) 50% 50%, radial-gradient(circle closest-side,#e7e7e7 90%,#0000) 100% 50%;

  background-size:calc(100%/3) 12px;
  background-repeat: no-repeat;
  animation:d3 2s infinite linear;
}
@keyframes d3 {
    20%{background-position:0%   0%, 50%  50%,100%  50%}
    40%{background-position:0% 100%, 50%   0%,100%  50%}
    60%{background-position:0%  50%, 50% 100%,100%   0%}
    80%{background-position:0%  50%, 50%  50%,100% 100%}
}
/*********************************************** custom LOADER ************************************************* */



/************************************************ BAG LOGO ************************************************/
.bag-container {
  width: 300px;
  height: 300px;
  /* background-color: #e5e5e5; */
  display: grid;
  place-items: center;
  /* to place it in background with animation */
  /* position: absolute;
  right: 10%; */
  animation: float 3s infinite ease-in-out;
  transition: all ease;
}
@keyframes float {
  0% {
    transform: translate(0, 0);
  }
  50% {
    transform: translate(0, 15px);
  }
  100% {
    transform: translate(0, -0);
  }
}
.bag {
  width: 100px;
  height: 88px;
  z-index: 1;
  position: relative;
  background-color: #ffc107;
  border: 3px solid black;
  transform: perspective(314px) rotateX(10deg);
  box-shadow: 1px -3px 0px #e5e5e5;
  box-shadow: 1px -2px 0px #dedede, 4px -5px 0px #000000;
  /* 2px -2px 0px #dedede, 5px -5px 0px #000000 */
}
.bag::before,
.bag::after {
  content: "";
  position: absolute;
  background-color: black;
  width: 3px;
  height: 3px;
}
.bag::before {
  top: 6%;
  left: 7%;
}
.bag::after {
  top: 6%;
  right: 7%;
}
.handle {
  width: 80px;
  height: 80px;
  /* background-color: #e5e5e5; */
  border-radius: 50%;
  position: absolute;
  bottom: 53%;
  /* left: 9%; */
  left: 13%;
  border: 3px solid black;
}
.glass {
  width: inherit;
  height: inherit;
  position: absolute;
  z-index: 2;
  background: rgba(255, 255, 255, 0.13);
  border-radius: 16px;
  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
  backdrop-filter: blur(3.7px);
  -webkit-backdrop-filter: blur(3.7px);
}
/************************************************ BAG LOGO ************************************************/

