/*,,,,,,,,,,,,,,,,,,,,,,,/ product 2 \,,,,,,,,,,,,,,,,,,,,,,,,*/
.cardN {
  background-color: var(--color1);
  color: var(--color2);
  width: 320px;
  height: 450px;
  letter-spacing: 1px;
  border-radius: 20px;
  overflow: hidden;
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
}

/* 'Nike' label in the background */
.cardN::before {
  color: var(--color3);
  width: fit-content;
  height: fit-content;
  font-size: 200px;
  font-weight: bold;
  font-style: italic;
  opacity: 15%;

  content: "Nike";
  position: absolute;
  left: 50%;
  top: 60%;
  transform: translate(-50%, -50%);
}

/* Circle */
.cardN::after {
  background-color: var(--color3);
  width: 400px;
  height: 400px;
  border-radius: 50%;
  z-index: 0;

  /* Transition */
  transition-duration: calc(var(--transitionDuration) / 2);
  transition-property: width, height, transform;
  transition-timing-function: ease-in-out;

  content: "";
  position: absolute;
  right: 0;
  top: 0;
  transform: translate(45%, -45%);
}

.cardN:hover::after {
  width: 500px;
  height: 500px;
  transform: translate(30%, -62%);
}

.card__img {
  width: 80%;
  /* height: 130px; */
  z-index: 1;
  transition: top var(--transitionDuration);

  /* Position */
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}

.cardN:hover .card__img {
  top: 24%;
}

.card__title {
  font-weight: 600;
  transition: top var(--transitionDuration);
  position: absolute;
  /* left: 50%; */
  top: 80%;
  /* transform: translateX(-50%); */
}

.cardN:hover .card__title {
  top: 50%;
}

.card__content {
  height: 70px;
  font-size: 14px;
  font-weight: 300;
  text-transform: uppercase;
  opacity: 0;
  transition: top var(--transitionDuration),
    opacity var(--transitionDuration) calc(var(--transitionDuration) / 2);

  /* Position */
  position: absolute;
  left: 50%;
  top: 150%;
  transform: translate(-50%, -50%);

  /* Flex */
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.cardN:hover .card__content {
  opacity: 100%;
  top: 70%;
}

.card__sizeContainer {
  /* Flex */
  display: flex;
  justify-content: center;
  align-items: center;
}

.card__sizeNumber {
  background-color: var(--color2);
  color: var(--color1);
  width: 26px;
  height: 26px;
  padding: 5px 7px;
  margin: 0 5px;
  font-size: 14px;
  border-radius: 6px;
  cursor: pointer;

  /* Flex */
  display: flex;
  justify-content: center;
  align-items: center;
}

.card__sizeNumber:hover {
  background-color: var(--color3);
}

.card__colorContainer {
  /* Flex */
  display: flex;
  justify-content: center;
  align-items: center;
}

.card__colorCircle {
  background-color: var(--color2);
  color: var(--color1);
  width: 20px;
  height: 20px;
  margin: 0 5px;
  border-radius: 50%;
  cursor: pointer;
}

.card__colorCircle:hover {
  outline: solid 2px var(--color2);
}

.card__link {
  background-color: var(--color2);
  color: var(--color1);
  padding: 10px 15px;
  text-decoration: none;
  font-weight: 600;
  border-radius: 3px;
  transition: top var(--transitionDuration);

  /* Position */
  position: absolute;
  left: 50%;
  top: 250%;
  transform: translate(-50%, -50%);
}

.cardN:hover .card__link {
  top: 88%;
}

.card__link:hover {
  background-color: var(--color3);
}
